<template>
  <view class="form-container">
    <!-- 性别选择 -->
    <view class="form-group">
      <text class="label">性别<text class="required">*</text></text>
      <radio-group @change="handleGenderChange">
        <label v-for="item in genderOptions" :key="item.value" class="radio-item">
          <radio :value="item.value" :checked="form.gender === item.value" color="#4CAF50" />
          <text>{{ item.label }}</text>
        </label>
      </radio-group>
    </view>

    <!-- 出生日期（使用原生picker） -->
    <view class="form-group">
      <text class="label">出生日期<text class="required">*</text></text>
      <picker 
        mode="date" 
        :value="form.birthdate" 
        :end="getToday()"
        @change="handleDateChange"
      >
        <view class="picker-input">
          {{ form.birthdate || '请选择出生日期' }}
          <text v-if="age !== null" class="age-display">（{{ age }}岁）</text>
        </view>
      </picker>
    </view>

    <!-- 身高输入 -->
    <view class="form-group">
      <text class="label">身高（cm）<text class="required">*</text></text>
      <input 
        type="number" 
        v-model="form.height" 
        placeholder="请输入身高"
        class="input-field"
        @input="validateNumber('height', 50, 250)"
      />
    </view>

    <!-- 体重输入 -->
    <view class="form-group">
      <text class="label">体重（kg）<text class="required">*</text></text>
      <input 
        type="number" 
        v-model="form.weight" 
        placeholder="请输入体重"
        class="input-field"
        @input="validateNumber('weight', 10, 300)"
      />
    </view>

    <!-- 提交按钮 -->
    <button 
      class="submit-btn" 
      @tap="handleSubmit" 
      :disabled="!isFormValid"
    >提交健康数据</button>

    <!-- 状态提示 -->
    <view v-if="submitStatus" :class="['status-message', submitStatus.type]">
      {{ submitStatus.message }}
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        gender: '',
        birthdate: '',
        height: null,
        weight: null
      },
      age: null,
      genderOptions: [
        { value: 'male', label: '男' },
        { value: 'female', label: '女' },
        { value: 'other', label: '其他' },
        { value: 'secret', label: '保密' }
      ],
      submitStatus: null
    }
  },
  computed: {
    isFormValid() {
      return Object.values(this.form).every(v => v !== '' && v !== null)
    }
  },
  methods: {
    // 日期处理
    getToday() {
      return new Date().toISOString().split('T')[0]
    },
    handleDateChange(e) {
      this.form.birthdate = e.detail.value
      this.calculateAge()
    },

    // 数字验证
    validateNumber(field, min, max) {
      let value = parseFloat(this.form[field])
      if (isNaN(value)) {
        this.form[field] = null
        return
      }
      this.form[field] = Math.max(min, Math.min(max, value))
    },

    // 提交逻辑
    async handleSubmit() {
      if (!this.isFormValid) {
        this.submitStatus = { type: 'error', message: '请填写所有必填字段' }
        return
      }

      try {
        // 使用 uni-app 的请求方法
        const res = await uni.request({
          url: '/api/submit-health-data',
          method: 'POST',
          data: this.form
        })

        if (res.statusCode === 200) {
          this.submitStatus = { type: 'success', message: '提交成功！' }
          this.resetForm()
        }
      } catch (error) {
        this.submitStatus = { type: 'error', message: '提交失败' }
      }
    }
  }
}
</script>

