  display: block;
  font-size: 32upx;
  color: #333;
  margin-bottom: 20upx;
}

.required {
  color: #ff4444;
}

.radio-item {
  display: flex;
  align-items: center;
  margin-right: 40upx;
  font-size: 28upx;
}

.picker-input {
  padding: 24upx;
  border: 2upx solid #e0e0e0;
  border-radius: 12upx;
}

.input-field {
  padding: 24upx;
  border: 2upx solid #e0e0e0;
  border-radius: 12upx;
  font-size: 28upx;
}

.submit-btn {
  background-color: #4CAF50;
  color: white;
  font-size: 32upx;
  padding: 28upx;
  border-radius: 12upx;
  margin-top: 40upx;
}

.submit-btn[disabled] {
  background-color: #cccccc;
}
</style>